.imgBtn {
    display: flex;
    align-items: center;
    border: none;
    padding-top: 0.4em;
    padding-bottom: 0.4em;
    margin-left: 1em;
    border-radius: 1em;
    -webkit-border-radius: 1em;
    -moz-border-radius: 1em;
    -ms-border-radius: 1em;
    -o-border-radius: 1em;
}
.imgBtn > img {
    height: 2.5em;
    width: auto;
}

@media (max-width: 600px) {
    .imgBtn > img {
        height: 2em;
    }
}